<template>
  <div>
    <loading-spinner v-if="loading"></loading-spinner>
    <div class="page-container" v-else>
      <client-data />
      <project-data />
      <div class="page-box">
        <div class="page-box-title">
          <h3>Ruimtes</h3>
          <div class="flex-divider"></div>
          <base-button @click="openCreateModal" class="small-on-mobile">
            <base-icon iconRef="plus" position="left" />
            <span class="button-label">Nieuwe Ruimte</span>
          </base-button>
        </div>
        <div class="page-box-datatable-container">
          <rooms-data-list :dataList="currentProjectRooms" @click="dataEntryClick($event)" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import clientData from "./client-page-components/client-data";
import projectData from "./client-page-components/project-data";
import roomsDataList from "./data-list-components/rooms-data-list.vue";
import LoadingSpinner from "./LoaderSpinner.vue";

import Firebase from "firebase";

export default {
  name: "Project",
  components: {
    clientData,
    projectData,
    roomsDataList,
    LoadingSpinner,
  },
  data() {
    return {
      loading: true,
      currentProject: null,
      currentClient: null,
      currentProjectRooms: [],
    };
  },
  methods: {
    openCreateModal() {
      this.$store.commit("setModalShown", {
        dataType: "room",
        modalType: "create",
        shown: true,
      });
    },
    dataEntryClick(value) {
      this.openRoom(value);
    },
    openRoom(targetRoomId) {
      this.$router.push(`/ruimte/${targetRoomId}`);
    },
  },
  watch: {
    currentClient() {
      this.$store.commit("setCurrentClientPageClient", this.currentClient);
    },
    currentProject() {
      this.$store.commit("setCurrentClientPageProject", this.currentProject);
    },
  },
  mounted() {
    Firebase.firestore()
      .collection("projects")
      .doc(this.$route.params.projectId)
      .get()
      .then((doc) => {
        this.currentProject = doc;
        this.$setPageTitle(
          `Project - ${this.currentProject.data().projectName}`
        );
        return doc;
      })
      .then((doc) => {
        Firebase.firestore()
          .collection("rooms")
          .where("belongsTo", "==", doc.id)
          .where("trashed", "==", false)
          .get()
          .then((snapshot) => {
            snapshot.forEach((doc) => {
              this.currentProjectRooms.push(doc);
            });
            return;
          })
          .then(() => {
            Firebase.firestore()
              .collection("clients")
              .doc(this.currentProject.data().belongsTo)
              .get()
              .then((doc) => {
                this.currentClient = doc;
                this.$store.commit("setCurrentClientPage", {
                  client: this.currentClient,
                  project: this.currentProject,
                });
                this.loading = false;
              });
          });
      });
  },
  destroyed: function () {
    this.$store.commit("setCurrentClientPage", {});
  },
};
</script>

<style>
</style>



